import Slider from "react-slick";
import { useState } from "react";

import "./HotelsCards.scss";

function HotelsCards(props) {
    const [windowWidth, setWindowWidth] = useState(window.innerWidth);

    const settings = {
        dots: true,
        infinite: true,
        slidesToScroll: 1,
        autoplay: true,
        speed: 500,
        autoplaySpeed: 3000,
    };

    function firstLetterSizeIncrease(string) {
        let firstLetter = string[0];
        let wordWithoutFirst = string.slice(1, string.length);
        return (
            <p className="hotel-desc-card-hotelName">
                <span style={{ fontSize: "4.4rem", fontWeight: "300" }}>
                    {firstLetter}
                </span>
                {wordWithoutFirst}
                <br />
                <span style={{ fontSize: "1.8rem", letterSpacing: "2px" }}>
                    {props.d?.city}, {props.d?.country}
                </span>
            </p>
        );
    }

    window.addEventListener("resize", () => {
        setWindowWidth(window.innerWidth);
    });

    return (
        <>
            <div className="hotel-collab-container">
                {windowWidth > 750 ? (
                    <div className="hotel-collab-card-container">
                        <div
                            className="hotel-desc-card"
                            style={{
                                backgroundImage: `linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.9) 100%), url(${props.d.pictures[0].url})`,
                            }}
                        >
                            {firstLetterSizeIncrease(props.d?.name)}
                        </div>
                        <div
                            className="hotel-desc-card hotel-desc-card-logo"
                            style={{
                                backgroundImage: `url(${props.d.brand_logo_url})`,
                                backgroundSize: '70%',
                                backgroundPosition: 'center',
                            }}
                        ></div>
                        <div className="hotel-desc-card">
                            <div className="hotel-desc-card-fact-container">
                                <p className="hotel-desc-card-fact-title">
                                    fact
                                </p>
                                <p className="hotel-desc-card-fact-text">
                                    {props.d.tip}
                                </p>
                            </div>
                        </div>
                        <div
                            className="hotel-desc-card"
                            style={{
                                backgroundImage: `url(${props.d.pictures[2].url})`,
                            }}
                        ></div>
                        <div className="hotel-desc-card">
                            <div className="hotel-desc-card-info-container">
                                <div className="hotel-desc-card-info-middle">
                                    <p className="hotel-desc-card-info-middle-title">
                                        style
                                    </p>
                                    <p className="hotel-desc-card-info-middle-text">
                                        {props.d.style}
                                    </p>
                                </div>
                                <div className="hotel-desc-card-info-middle">
                                    <p className="hotel-desc-card-info-middle-title">
                                        Accomodation
                                    </p>
                                    <p className="hotel-desc-card-info-middle-text">
                                        {props.d.accomodation}
                                    </p>
                                </div>
                                <div className="hotel-desc-card-info-middle">
                                    <p className="hotel-desc-card-info-middle-title">
                                        Best Part
                                    </p>
                                    <p className="hotel-desc-card-info-middle-text">
                                        {props.d.best_part}
                                    </p>
                                </div>
                                <div className="hotel-desc-card-info-bottom">
                                    <p className="hotel-desc-card-info-bottom-title">
                                        Location
                                    </p>
                                    <p className="hotel-desc-card-info-bottom-text">
                                        {props.d.location}
                                    </p>
                                </div>
                            </div>
                        </div>

                        <div
                            className="hotel-desc-card"
                            style={{
                                backgroundImage: `url(${props.d.pictures[3].url})`,
                            }}
                        ></div>
                    </div>
                ) : (
                    <div className="hotel-collab-card-container">
                        <div className="hotel-collab-card-carousel-overlay">
                                {firstLetterSizeIncrease(props.d.name)}
                        </div>
                        <Slider {...settings}>
                            <div className="hotel-desc-card">
                                <img
                                    src={`${props.d.pictures[0].url}`}
                                    alt=""
                                />
                            </div>
                            <div className="hotel-desc-card">
                                <div
                                    style={{
                                        background: `url(${props.d.brand_logo_url})`,
                                        height: "100%",
                                        width: "100%",
                                        backgroundRepeat: "no-repeat",
                                        backgroundSize: "60%",
                                        backgroundPosition: "center",
                                    }}
                                ></div>
                            </div>
                            <div className="hotel-desc-card">
                                <img
                                    src={`${props.d.pictures[2].url}`}
                                    alt=""
                                />
                            </div>
                            <div className="hotel-desc-card">
                                <img
                                    src={`${props.d.pictures[3].url}`}
                                    alt=""
                                />
                            </div>
                        </Slider>

                        <div className="hotel-desc-card">
                            <div className="hotel-desc-card-fact-container">
                                <p className="hotel-desc-card-fact-title">
                                    fact
                                </p>
                                <p className="hotel-desc-card-fact-text">
                                    {props.d.tip}
                                </p>
                            </div>
                        </div>
                        <div className="hotel-desc-card">
                            <div className="hotel-desc-card-info-container">
                                <div className="hotel-desc-card-info-middle">
                                    <p className="hotel-desc-card-info-middle-title">
                                        style
                                    </p>
                                    <p className="hotel-desc-card-info-middle-text">
                                        {props.d.style}
                                    </p>
                                </div>
                                <div className="hotel-desc-card-info-middle">
                                    <p className="hotel-desc-card-info-middle-title">
                                        Accomodation
                                    </p>
                                    <p className="hotel-desc-card-info-middle-text">
                                        {props.d.accomodation}
                                    </p>
                                </div>
                                <div className="hotel-desc-card-info-middle">
                                    <p className="hotel-desc-card-info-middle-title">
                                        Best Part
                                    </p>
                                    <p className="hotel-desc-card-info-middle-text">
                                        {props.d.best_part}
                                    </p>
                                </div>
                                <div className="hotel-desc-card-info-bottom">
                                    <p className="hotel-desc-card-info-bottom-title">
                                        Location
                                    </p>
                                    <p className="hotel-desc-card-info-bottom-text">
                                        {props.d.location}
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            </div>
            <hr className="hotel-card-hr" />
        </>
    );
}

export default HotelsCards;
